
.file-input-button{
    display: block;
    background: #0087ff;
    padding: 20px 0;
    text-align: center;
    color: #fff;
    border-radius: 3px;
}
.disabled{
    background: #ccc;
}
#file-input{
    opacity: 0;
    display: none;
}
.unpack-api-ipa-info{
    margin: 0 auto;
    padding: 80px;
}
table{
    width: 100%;
}
table tr{
    padding: 15px;
}
table tr:hover{
    background: #eee;
}
table th{
    padding: 5px;
    text-align: right;
    border: 1px solid #eee;
    font-weight: normal;
    color: #444;
}
table td{
    width: 80%;
    text-indent: 2em;
    border: 1px solid #eee;
}
